import { Component, ViewChild } from '@angular/core';
import { IonicPage,Tabs, NavController, NavParams, AlertController, Slides,Platform } from 'ionic-angular';
import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular/util/events';



@IonicPage()
@Component({
  selector: 'page-community-list',
  templateUrl: 'community-list.html',
})
export class CommunityListPage {
  secondIonScroll: boolean;
  firstIonScroll: boolean;
  deviceId: any;
  login_token: any;
  employeeId: any;
  value: number = 0;
  communityList: any;
  community:any;
  myinfinte:any;
  communityListErrorMsg: string;
  myCommunityList:any;
  myCommInfinte:any;
  myComvalue: number = 0;
  myCommunityListErrorMsg: string;
  alert121:any;
  public unregisterBackButtonAction: any;
  closeAppPopupShow:number=0;
  tab:Tabs;
  constructor(private storage: Storage, private apiServices: ApiServiceProvider,
    private alertCtrl: AlertController, public events: Events,private platform: Platform,
    public navCtrl: NavController, public navParams: NavParams) {
      this.tab = this.navCtrl.parent;
      this.community='allcomunity';
      this.firstIonScroll =true;
      this.secondIonScroll = true;
    this.storage.get('employeeId').then(data => {
      this.employeeId = data;
      console.log(' employeeId== ', this.employeeId);
    });
    this.storage.get('login_token').then((data) => {
      console.log('thoughtOftheDayApi value is111==', data);
      this.login_token = data;
    })

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad thoughtOftheDayApi');
    this.getcommuListFun();
    // this.getMycommuListFun();
  }

  getcommuListFun() {
    if(this.secondIonScroll){
      this.myCommunityList=[];
    }

    if(this.firstIonScroll){
      this.myComvalue=0;
      this.storage.get('employeeId').then(data => {
        this.employeeId = data;
        console.log(' employeeId== ', this.employeeId);
      });
      this.storage.get('login_token').then((data) => {
        console.log('showOnBoard value is111==', data);
        this.login_token = data;
      })
      this.storage.get('deviceId').then(data => {
      this.deviceId = data;
      let apiKey = {
        "client_id": this.apiServices.clientId,
        "employee_id": this.employeeId,
        "device": this.apiServices.device,
        "device_id": this.deviceId,
        "app_version": this.apiServices.appVersion,
        "value":this.value
      };
      this.apiServices.communityListApi(apiKey, this.login_token)
        .subscribe((res) => {
          console.log('getcommuListFun List res==', res);
          if (res.success == 1) {
            if (this.communityList == undefined || this.communityList.length==0) {

              this.communityList = res.data;
              console.log('getcommuListFun List data==', this.communityList);
            } else {
              this.communityList=this.communityList.concat(res.data);
              console.log('new data== ', this.communityList);
              this.myinfinte.complete();
            }
            this.communityListErrorMsg='';

          } else {
            if(this.value==0){
              this.communityListErrorMsg=res.message;
            }
            this.myinfinte.complete();
            this.communityListErrorMsg=res.message;
            this.firstIonScroll=false;
            
          }
        })
      });
    } else{

    }
  }

  getMycommuListFun(){
    if(this.firstIonScroll){
      this.communityList=[];
    }

    if(this.secondIonScroll){
      this.value=0;  
      this.storage.get('employeeId').then(data => {
        this.employeeId = data;
        console.log(' employeeId== ', this.employeeId);
      });
      this.storage.get('login_token').then((data) => {
        console.log('showOnBoard value is111==', data);
        this.login_token = data;
      })
      this.storage.get('deviceId').then(data => {
        this.deviceId = data;
        let apiKey = {
          "client_id": this.apiServices.clientId,
          "employee_id": this.employeeId,
          "device": this.apiServices.device,
          "device_id": this.deviceId,
          "app_version": this.apiServices.appVersion,
          "value":this.myComvalue,
          "myCommunity":"1"
        };
        this.apiServices.communityListApi(apiKey, this.login_token)
          .subscribe((res) => {
            console.log('getcommuListFun List res==', res);
            if (res.success == 1) {
              if (this.myCommunityList == undefined || this.myCommunityList.length==0) {
                this.myCommunityList = res.data;
                console.log('getcommuListFun List data==', this.myCommunityList);
              } else {
                this.myCommunityList=this.myCommunityList.concat(res.data);
                console.log('new data== ', this.myCommunityList);
                this.myCommInfinte.complete();
              }
              this.myCommunityListErrorMsg='';
            } else {
              this.myCommInfinte.complete();
              this.myCommunityListErrorMsg=res.message;
              this.secondIonScroll=false;        
            }
          })
      });
    } else {

    }
   
  }

  doInfinite(infiniteScroll) {
    this.myinfinte=infiniteScroll;
    console.log('Begin async operation');
    setTimeout(() => {
      this.value=this.communityList.length;
      this.getcommuListFun();
     
    }, 500);
  }
  
  myCommdoInfinite(infiniteScroll) {
    this.myCommInfinte=infiniteScroll;
    console.log('Begin async operation');
    setTimeout(() => {
      this.myComvalue=this.myCommunityList.length;
      this.getMycommuListFun();
     
    }, 500);
  }


  makeMember(comdata,index?:number){  
    console.log('data**==', comdata);
    if(comdata.join_status==1||comdata.join_status=='1'){
      comdata.total_members=parseInt(comdata.total_members)-1;
      comdata.join_status=0;
      comdata.total_members_new = parseInt(comdata.total_members_new)-1;
      // this.communityList[index].join_status = 0;
    }else{
      comdata.total_members_new = parseInt(comdata.total_members_new) + 1;
      // this.communityList[index].join_status = 1;
      comdata.total_members=parseInt(comdata.total_members)+1;
      comdata.join_status=1;
    }
    this.storage.get('employeeId').then(data => {
      this.employeeId = data;
      console.log(' employeeId== ', this.employeeId);
    });
    this.storage.get('login_token').then((data) => {
      console.log('showOnBoard value is111==', data);
      this.login_token = data;
    })
    this.storage.get('deviceId').then(data => {
      this.deviceId = data;
      let apiKey = {
        "client_id": this.apiServices.clientId,
        "employee_id": this.employeeId,
        "device": this.apiServices.device,
        "device_id": this.deviceId,
        "app_version": this.apiServices.appVersion,
        "community_id":comdata.communityId
      }
      console.log('key== ', apiKey);
      this.apiServices.communityJoinUnJoinedApi(apiKey, this.login_token)
        .subscribe((res) => {
          console.log('communityJoinUnJoinedApi==', res);
          if (res.success == 1) {
            this.events.publish('communnityAddRemove',comdata);
          } else {
            
          }
        })
    });

  }

  removeFromMember(data, index){
    this.myCommunityList.splice(index,1);
    this.makeMember(data);
  }

  seeCommuPost(data){
    this.navCtrl.push('CommunityPostsPage',{'data':data});
  }

  ionViewWillEnter(){
    this.initializeBackButtonCustomHandler();
  }
  
  
  showeAlert() {
    this.alert121 = this.alertCtrl.create({
      title: 'App termination',
      message: 'Do you want to close the app?',
      buttons: [{
        text: 'Cancel',
        role: 'cancel',
        handler: () => {
          console.log('Application exit prevented!');
        }
      }, {
        text: 'Close App',
        handler: () => {
          this.platform.exitApp(); // Close this application
        }
      }]
    });
    this.alert121.present();
  }
  
  ionViewWillLeave() {
    this.unregisterBackButtonAction && this.unregisterBackButtonAction();
  }
  
  //Hardware Back Button
  initializeBackButtonCustomHandler(): void {
    let that = this;
    this.unregisterBackButtonAction = this.platform.registerBackButtonAction(function (event) {
      // that.closeAppPopupShow++;
      // if(that.closeAppPopupShow%2!=0){
      //   that.showeAlert();
      // }else{
      //   that.alert121.dismiss();
      // }
      that.navCtrl.parent.select(0);
      // that.tab.select(1);

    }, 101); // Priority 101 will override back button handling (we set in app.component.ts) as it is bigger then priority 100 configured in app.component.ts file */
  }

}



